legend {
  margin: 3rem 0;
}
body.documentation > div.container {
  margin-bottom: 80px;  
}
.documentation {
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
}
.documentation h2 {
  font-family: 'Raleway', sans-serif;
  font-size: 30px;
  font-weight: bold;
  color: #70903b;
}
.documentation h3 {
  font-family: 'Raleway', sans-serif;
  font-size: 24px;
  color: #70903b;
}
.doc-typografy {
  list-style: none;
  line-height: 4rem;
}
.content-jumb {
  margin-bottom: 0;
  background: white;
  border: 2px solid rgb(234, 228, 228);
}
.c-red {
  color: red;
}
.c-blue {
  color: blue;
}



.list-dataTable li {
  list-style: none;
}
.list-d3 li {
  list-style: none;
}

/* Base16 Atelier Dune Dark - Theme */
/* by Bram de Haan (http://atelierbram.github.io/syntax-highlighting/atelier-schemes/dune) */
/* Original Base16 color scheme by Chris Kempson (https://github.com/chriskempson/base16) */

/* Atelier-Dune Comment */
.hljs-comment {
  color: #999580;
}

/* Atelier-Dune Red */
.hljs-variable,
.hljs-attribute,
.hljs-tag,
.hljs-regexp,
.hljs-name,
.ruby .hljs-constant,
.xml .hljs-tag .hljs-title,
.xml .hljs-pi,
.xml .hljs-doctype,
.html .hljs-doctype,
.css .hljs-id,
.css .hljs-class,
.css .hljs-pseudo {
  color: #d73737;
}

/* Atelier-Dune Orange */
.hljs-number,
.hljs-preprocessor,
.hljs-built_in,
.hljs-literal,
.hljs-params,
.hljs-constant {
  color: #b65611;
}

/* Atelier-Dune Yellow */
.ruby .hljs-class .hljs-title,
.css .hljs-rule .hljs-attribute {
  color: #ae9513;
}

/* Atelier-Dune Green */
.hljs-string,
.hljs-value,
.hljs-inheritance,
.hljs-header,
.ruby .hljs-symbol,
.xml .hljs-cdata {
  color: #60ac39;
}

/* Atelier-Dune Aqua */
.hljs-title,
.css .hljs-hexcolor {
  color: #1fad83;
}

/* Atelier-Dune Blue */
.hljs-function,
.python .hljs-decorator,
.python .hljs-title,
.ruby .hljs-function .hljs-title,
.ruby .hljs-title .hljs-keyword,
.perl .hljs-sub,
.javascript .hljs-title,
.coffeescript .hljs-title {
  color: #6684e1;
}

/* Atelier-Dune Purple */
.hljs-keyword,
.javascript .hljs-function {
  color: #b854d4;
}

.hljs {
  display: block;
  overflow-x: auto;
  background: #20201d;
  color: #a6a28c;
  padding: 0.5em;
  -webkit-text-size-adjust: none;
}

.coffeescript .javascript,
.javascript .xml,
.tex .hljs-formula,
.xml .javascript,
.xml .vbscript,
.xml .css,
.xml .hljs-cdata {
  opacity: 0.5;
}
