// MENU GENERAL
.main {
	width: 183px;
	position: fixed;
	top: 6.79em;
	color: white;
	background: @color-main;
	-webkit-transition: .4s;
	-moz-transition: .4s;
	-o-transition: .4s;
	transition: .4s;
	z-index: 99999; 
}
.main.collap {
	width: 55px;
	-webkit-transition: .4s;
	-moz-transition: .4s;
	-o-transition: .4s;
	transition: .4s;
}
// BOTON EXPANDIR/COLAPSAR MENU
.main-content-btn {
	position: relative;
	height: 50px;
	background: #565656;
}
.btn-main {
	position: absolute;
	font-size: 2rem;
	left: 1.68rem;
	line-height: 50px;
}
// COMPAÑIA ANALIZADA MENU
.associate-company {
	text-align: center;
	font-weight: bold;
	padding: 1.5rem 0 1rem;
	font-family: 'Raleway', sans-serif;
	font-size: 1.70rem;
	font-weight: bold;
}
.main-content-btn,
.associate-company,
.main-category {
	box-shadow: 0px 1px 0px #55822c;
}
#CA > p:nth-child(2) {
	font-size: 12px;
	font-family: 'Roboto', sans-serif;
}
// CATEGORIAS MENU
.main-category {
	cursor: pointer;
	text-align: center;
	font-weight: bold;
	height: 50px;
  line-height: 50px;
  position: relative;
}
.main-category p {
	margin-bottom: 0;
}
.title-category {
  font-weight: 500;
}
.title-val,
.title-diag {
	margin-left: 1rem;
}
.title-proj {
	margin-left: 2.7rem;
}
.associate-company:hover,
.main-category:hover,
.abreviators {
	background: @color-main-hover;
	box-shadow: 0px 1px 0px #55822c;
}
// SUBMENUS DE CATEGORIAS MENU
.main ul {
	display: none;
}
.main ul li a {
	background: @color-submain;
	color: white;
	height: 34px;
	line-height: 34px;
	padding: 0 15px;
	text-align: left;
}
.collap {	
	padding: 0 !important;
	text-align: center !important;
}
.select {
	background: @color-submain-hover;
}
.main-content-btn:hover,
.main ul li a:hover,
.main ul li a:active,
.main ul li a:focus,
.main ul li a:visited {
	background: @color-submain-hover;
}
.submain-diag li,
.submain-proj li,
.submain-val li {
	margin-top: 0 !important;
}
.main .nav-pills > li > a {
  border-radius: 0!important; 
}
// ABREVIATURAS MENU
.abreviators {
	padding: 15px;
  font-size: 11px;
  line-height: 18px;
}
// ICON-FONTS MENU
.icon-category {
	font-size: 2.5rem;
	line-height: 5rem; 
	position: absolute;
	left: 1.2rem;
}
.icon-category.extend {
	position: relative;
  left: 0px;
  top: 0.5rem;
  line-height: 1.8rem;
}
.info-icon {
	font-size: 2.5rem;
}
// PSEUDOELEMENTOS CSS 
#btn-collapsed-diag::before,
#btn-collapsed-proj::before,
#btn-collapsed-val::before {
	content: " ";
	position: absolute;
	display: none;
	width: 0px;
	height: 0px;
	top: 1.3em;
	right: 0px;
	border-top: @color-main 6px solid;
	border-right: white 7px solid;
	border-bottom: @color-main 6px solid;
	border-left: white 0px solid;
}
#btn-collapsed-diag:hover::before,
#btn-collapsed-proj:hover::before, 
#btn-collapsed-val:hover::before {
	content: " ";
	position: absolute;
	display: block;
	width: 0px;
	height: 0px;
	top: 1.3em;
	right: 0px;
	border-top: @color-main-hover 6px solid;
	border-right: white 7px solid;
	border-bottom: @color-main-hover 6px solid;
	border-left: white 0px solid;
}



/*

*/
#content {
	padding-top: @height-header;
	padding-left: 216px;
	padding-right: 15px;
	margin-bottom: 5rem;
	height: auto;
	min-height: 100%;
	-webkit-transition: .4s;
	-moz-transition: .4s;
	-o-transition: .4s;
	transition: .4s;
}
#content.contentTotal{
	padding-left: 88px;
	-webkit-transition: .4s;
	-moz-transition: .4s;
	-o-transition: .4s;
	transition: .4s;
}
#content:after {
	height: 63px;
	display: block;
	clear: both;
}
@media screen and (min-width: 1400px) {
	#content {
		padding: 5.5rem 22rem;
	 	padding-left: 22rem !important;
	}
}